@import '/src/styles/variables';
.layout{
    position: fixed;
    inset: 0;
    display: flex;
    flex-direction: column;

    &__header{
        position: fixed;
        width: 100%;
        top: 0;
        min-height: 65px;
        max-height: 65px;
        background: $color-blue;
        color: $color-white;
        display: flex;
        align-items: center;
        padding: 0px 30px;
        font-size: 20px;
        gap: 15px;
        display: flex;

        box-shadow: $shadow;
    }
    &__user-menu{
        margin-left: auto;
        
    }

    &__body{
        margin-top: 75px;
        padding: 0 30px;
        overflow: auto;
    }
}