@import 'src/styles/variables.scss';

.container {
    box-sizing: border-box;
    display: flex;
    gap: 4px;
    flex-direction: column;
   
    &__lbl{
    font-size: 14px;
    color: #777777;
    &_strong{
        font-weight:600 ;
    }
}



    &__input {
    border: none;
    border-bottom: 1px solid $color-blue;
    outline: none;
    padding: 0;
    font-size: 14px;
    }

    &__info{
    font-size: 12px;
    &_success{
        color: lightgreen;
    }
    
    &__info_info{
        color: lightblue;
    }

    &__info_error{
    color: red;
    }
    }

}