.button{
    display: flex;
    border: none;
    box-sizing: border-box;
    background: #0099d4;
    border-radius: 3px;
    min-height: 36px;
    font-size: 14px;
    padding: 0px 12px;
    cursor: pointer;
    font-weight: 600;
    min-width: 180px;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    
    border: 1px solid lightgray;
    color: black;
    &:hover{
        opacity: 0.8;
    }

    &_primary{
        background: #3369f3;
        color: #ffffff;
        border: none;
    }
    &_secondary{
        background: #ffffff;
        color: #3369f3;
        border: none;
    }
}

